import { BaseQuestion, ChoiceReportMixin } from './base'
import { Q_TRUE_FALSE } from './consts'


class TrueFalseOption {
  constructor(seq, answerSeq = 0) {
    this.seq = seq
    this.isAnswer = answerSeq === this.seq
    this.sids = []

    if (this.seq === 0) {
      this.seqLabel = ''
    }
    if (this.seq === 1) {
      this.seqLabel = ''
    }
  }

  validate() {
    if (![0, 1].includes(this.seq)) {
      return [false, '']
    }
  }

  isUserAnswer(studentId) {
    return this.sids.includes(studentId)
  }

  injectReportData(data) {
    this.sids = data[this.seq] || []
    this.hasReport = true
  }
}


class TrueFalse extends BaseQuestion {
  //  seqLabel  api  options 
  constructor(sourceData) {
    super(sourceData, Q_TRUE_FALSE)
    this.structureLevel = 1
    this.stemRich = sourceData.stemRich || {}
    this.analysisRich = sourceData.analysisRich || {}
    this.answerSeq = sourceData.answerSeq || 0

    this.options = []
    this.options.push(new TrueFalseOption(0, this.answerSeq))
    this.options.push(new TrueFalseOption(1, this.answerSeq))

    this.windup()
  }

  get answerSeqLabel() {
    if (this.answerSeq === 0) return ''
    if (this.answerSeq === 1) return ''
  }

  validate() {
    const error = super.validate()
    return error
  }
}

Object.assign(TrueFalse.prototype, ChoiceReportMixin)

export default TrueFalse



